import { SubtleButton } from '@/components/buttons/SubtleButton';
import DocumentUpload from '@/components/icons/document/DocumentUpload';
import UserEditIcon from '@/components/icons/user/UserEditIcon';
import { Dialog, DialogContent, DialogHeader } from '@/components/ui/dialog';
import { cn } from '@/lib/utils';
import AddVacationForm from '../../forms/addVacation/AddVacationForm';

interface Props {
    open: boolean;
    setAddVacation: (value: boolean) => void;
}

export default function AddVacation({ open, setAddVacation }: Props) {
    return (
        <Dialog open={open}>
            <div
                className={cn('fixed inset-0 bg-black/50', {
                    '!hidden': !open,
                })}
            />
            <DialogContent
                showCloseButton={false}
                className="max-w-[623px] rounded-lg p-8 bg-white-blue-50 font-cairo gap-0 [&_[aria-label=Close]]:opacity-0"
            >
                <DialogHeader className="flex flex-col items-center">
                    <div className="flex flex-row justify-between mb-4 w-full">
                        <div className="flex flex-row gap-2">
                            <UserEditIcon />
                            <h3 className="text-[22px] font-bold text-primery-blue-500">تخصيص اجازة</h3>
                        </div>
                        {/* <SubtleButton className="bg-primery-blue-50 text-base font-medium leading-[125%] text-primery-blue-500 border border-primery-blue-50">
                            <DocumentUpload />
                            لرفع مستند
                        </SubtleButton> */}
                    </div>
                    <div className="w-full border-b border-[#D1D1D1] !mt-0"></div>
                </DialogHeader>
                <AddVacationForm setAddVacation={setAddVacation} />
            </DialogContent>
        </Dialog>
    );
}
