import { z } from 'zod';

export const AddVacationSchema = z.object({
    employee_id: z.string().min(1, 'This field is required'),
    vacation_type: z.string().min(1, 'This field is required'),
    start_date: z.date({
        required_error: 'This field is required',
    }),
    end_date: z.date({
        required_error: 'This field is required',
    }),
    reason: z.string().min(1, 'This field is required'),
});

export type AddVacationFormValues = z.infer<typeof AddVacationSchema>;
