import { z } from 'zod';

export const AddAttendanceSchema = z.object({
    employee_id: z.string().min(1, 'Employee name field is required.'),
    date: z.date({
        required_error: 'Date is required',
    }),
    check_in: z.string().min(1, 'Check in is required'),
    check_out: z.string().min(1, 'Check out time is required'),
    status: z.string().min(1, 'Status is required'),
    notes: z.string().min(1, 'Notes is required'),
});

export type AddAttendanceFormValues = z.infer<typeof AddAttendanceSchema>;
