import { AttendanceType } from '../../types/employeeAttendance.types';

interface Props {
    attendance: AttendanceType;
    AttendanceCard: any;
}
export default function AttendanceListComponent({ attendance, AttendanceCard }: Props) {
    const attendanceArray = Object.entries(attendance).map(([date, details]) => ({
        date,
        ...details,
    }));

    return (
        <div>
            <div className="grid grid-cols-7 text-center border border-[#E8E8E8]">
                <p className="bg-primery-blue-50 border-e border-[#E8E8E8] p-[15px] font-semibold text-xs tracking-normal leading-[100%]">
                    السبت
                </p>
                <p className="bg-primery-blue-50 border-e border-[#E8E8E8] p-[15px] font-semibold text-xs tracking-normal leading-[100%]">
                    الاحد
                </p>
                <p className="bg-primery-blue-50 border-e border-[#E8E8E8] p-[15px] font-semibold text-xs tracking-normal leading-[100%]">
                    الإثنين
                </p>
                <p className="bg-primery-blue-50 border-e border-[#E8E8E8] p-[15px] font-semibold text-xs tracking-normal leading-[100%]">
                    الثلاثاء
                </p>
                <p className="bg-primery-blue-50 border-e border-[#E8E8E8] p-[15px] font-semibold text-xs tracking-normal leading-[100%]">
                    الاربعاء
                </p>
                <p className="bg-primery-blue-50 border-e border-[#E8E8E8] p-[15px] font-semibold text-xs tracking-normal leading-[100%]">
                    الخميس
                </p>
                <p className="bg-primery-blue-50 p-[15px] font-semibold text-xs tracking-normal leading-[100%]">
                    الجمعة
                </p>
            </div>
            <div className="grid grid-cols-7">
                {attendanceArray.map((attend, index: number) => (
                    <div key={index} className="">
                        <AttendanceCard attendance={attend} />
                    </div>
                ))}
            </div>
        </div>
    );
}
