import UnderlineHeading from '@/components/heading/UnderlineHeading';
import ArrowBackIcon from '@/components/icons/ArrowBackIcon';
import SearchIcon from '@/components/icons/Search';
import { Input } from '@/components/ui/input';
import { useEffect, useState } from 'react';
import { endpoints } from '@/lib/axios/axios-global';
import { apiGet } from '@/lib/axios/axios-utils';
import EmployeeInfo from './EmployeeInfo';
import AttendanceListComponent from './AttendanceListComponent';
import AttendanceCard from './AttendanceCard';
import AddEventDropdown from './AddEventDropDown';
import { useParams } from 'react-router-dom';
import { LoadingSpinner } from '@/components/Loading/LoadingSpinner';
import { EmployeeAttendanceType } from '../../types/employeeAttendance.types';
import { CustomSelect } from '@/components/dropDown/Dropdown';
import { Filters } from '@/types/table.types';

export default function EmployeeAttendance() {
    const [attendance, setAttendance] = useState<EmployeeAttendanceType>();
    const [filters, setFilters] = useState<Filters>({});
    const [search, setSearch] = useState('');
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);
    const { id } = useParams();

    useEffect(() => {
        const fetchAttendance = async () => {
            try {
                const response = await apiGet({
                    endpoint: endpoints.employeeAttendance.attendance,
                    queryParams: { employee_id: `${id}`, ...filters },
                });

                // setTotalItems(response.data.data.pagination.total);

                setAttendance(response.data.data[0]);
            } catch (error) {
                setError('Failed to load attendance');
            } finally {
                setLoading(false);
            }
        };

        fetchAttendance();
    }, [filters, search]);

    if (loading) {
        return <LoadingSpinner />;
    }

    if (error) {
        return (
            <div className="mt-6 mb-[102px] text-center py-20">
                <h2 className="text-xl text-red-500">{error}</h2>
            </div>
        );
    }

    if (!attendance) {
        return (
            <div className="mt-6 mb-[102px] text-center py-20">
                <h2 className="text-xl">attendance not found</h2>
            </div>
        );
    }

    const currentYear = new Date().getFullYear();
    const years = Array.from({ length: 10 }, (_, i) => currentYear - i).map((year) => ({
        label: year.toString(),
        value: year.toString(),
    }));

    return (
        <div className="mb-10 mt-6 border border-[#E8E7EA] rounded-lg">
            <div className="flex flex-col p-2 sm:flex-row sm:items-center sm:justify-between">
                <UnderlineHeading text="الحضور والانصراف للموظف" />
                <div className="flex flex-row items-center gap-4">
                    {/* <div className="flex flex-row items-center w-[383px] border border-white-blue-300 bg-white-blue-50 h-[36px] p-2 rounded-[6px]">
                        <Input
                            placeholder="ابحث باسم العميل"
                            className="text-white-blue-800 p-0 tracking-normal text-xs lg:text-sm lg:leading-[16.8px] border-none outline-none focus-visible:ring-0 placeholder:text-white-blue-800"
                            onChange={(e) => setSearch(e.target.value)}
                        />
                        <button className="bg-transparent border-none">
                            <SearchIcon className="text-white-blue-800" />
                        </button>
                    </div> */}
                    <CustomSelect
                        value={(filters?.month as string) || ''}
                        onValueChange={(value) => {
                            setFilters({ ...filters, month: value });
                        }}
                        placeholder="شهر"
                        width="min-w-[77px]"
                        options={[
                            { label: 'شهر', value: '' },
                            { label: 'يناير', value: '1' },
                            { label: 'فبراير', value: '2' },
                            { label: 'مارس', value: '3' },
                            { label: 'أبريل', value: '4' },
                            { label: 'مايو', value: '5' },
                            { label: 'يونيو', value: '6' },
                            { label: 'يوليو', value: '7' },
                            { label: 'أغسطس', value: '8' },
                            { label: 'سبتمبر', value: '9' },
                            { label: 'أكتوبر', value: '10' },
                            { label: 'نوفمبر', value: '11' },
                            { label: 'ديسمبر', value: '12' },
                        ]}
                    />
                    <CustomSelect
                        value={(filters?.year as string) || ''}
                        onValueChange={(value) => {
                            setFilters({ ...filters, year: value });
                        }}
                        placeholder="سنة"
                        width="min-w-[77px]"
                        options={[{ label: 'سنة', value: '' }, ...years]}
                    />
                    <div>
                        <AddEventDropdown />
                    </div>
                    <div className="flex items-center justify-center w-10 h-10 bg-primery-blue-50 border border-primery-blue-50 shadow-[0px_1px_2px_0px_#1018280D] rounded-lg ">
                        <ArrowBackIcon />
                    </div>
                </div>
            </div>
            <EmployeeInfo />
            <div className="mt-6">
                <AttendanceListComponent attendance={attendance.attendance} AttendanceCard={AttendanceCard} />
            </div>
        </div>
    );
}
