import ArrowDownIcon from '@/components/icons/ArrowDownIcon';
import ArrowUpIcon from '@/components/icons/ArrowUpIcon';
import {
    DropdownMenu,
    DropdownMenuContent,
    DropdownMenuItem,
    DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu';
import i18n from '@/lib/i18n/i18n';
import { useState } from 'react';
import AddAttendance from '../popups/AddAttendance';
import AddVacation from '../popups/AddVacation';

export default function AddEventDropdown() {
    const [isOpen, setIsOpen] = useState(false);
    const [addAttendance, setAddAttendance] = useState(false);
    const [addVacation, setAddVacation] = useState(false);

    return (
        <>
            <DropdownMenu onOpenChange={(open) => setIsOpen(open)} dir={i18n.dir()}>
                <DropdownMenuTrigger
                    className="flex flex-row items-center justify-center gap-2 h-9 text-base leading-5 font-semibold font-cairo tracking-normal bg-primery-blue-700 text-white-blue-50 px-2 py-1 rounded-lg text-nowrap
                hover:bg-primery-blue-500 
                focus:outline-none focus:ring-2 focus:ring-primery-blue-700 focus:ring-offset-2
                          disabled:bg-primery-blue-200 disabled:opacity-50"
                >
                    اضافة حدث
                    {isOpen ? <ArrowUpIcon /> : <ArrowDownIcon />}
                </DropdownMenuTrigger>
                <DropdownMenuContent className="min-w-24">
                    <DropdownMenuItem asChild>
                        <button
                            onClick={() => setAddAttendance(true)}
                            className="w-full cursor-pointer px-4 py-3 text-white-blue-900 text-base leading-5 font-semibold font-cairo tracking-normal"
                        >
                            اضافة حضور
                        </button>
                    </DropdownMenuItem>
                    <DropdownMenuItem asChild>
                        <button
                            onClick={() => setAddVacation(true)}
                            className="w-full cursor-pointer px-4 py-3 text-white-blue-900 text-base leading-5 font-semibold font-cairo tracking-normal"
                        >
                            تخصيص اجازة
                        </button>
                    </DropdownMenuItem>
                </DropdownMenuContent>
            </DropdownMenu>
            <AddAttendance open={addAttendance} setAddAttendance={setAddAttendance} />
            <AddVacation open={addVacation} setAddVacation={setAddVacation} />
        </>
    );
}
