import { cn } from '@/lib/utils';

interface Props {
    attendance: {
        status: 'present' | 'absent' | 'late' | 'leave';
        check_in: string | null;
        check_out: string | null;
        notes: string | null;
        date: string;
    };
}

const AttendanceCard = ({ attendance }: Props) => {
    return (
        <div className="border border-[#E8E8E8] p-2 w-full">
            <div>
                <p className="text-xl font-medium text-white-blue-700 tracking-normal">{attendance.date}</p>
            </div>
            <div className="mt-[6.3px] px-[13.3px]">
                <div className="flex flex-row gap-[2px] justify-start text-nowrap">
                    <p className="text-[11px] font-normal text-primery-blue-700 tracking-normal">نوع الاجازة:</p>
                    <p className="text-[11px] font-normal text-white-blue-800 tracking-normal">ايام العطله</p>
                </div>
                <div className="flex flex-row gap-[2px] justify-start text-nowrap">
                    <p className="text-[11px] font-normal text-primery-blue-700 tracking-normal">نظام الاجازة</p>
                    <p className="text-[11px] font-normal text-white-blue-800 tracking-normal">السعودي</p>
                </div>
            </div>
            <div className="mt-[6.3px] p-[2px]">
                <button
                    className={cn(
                        'text-center w-full py-1 rounded-sm text-xs font-medium text-[#D97706] leading-[115%] tracking-normal',
                        {
                            'bg-[#FFFBEB]': attendance.status === 'absent',
                            'bg-[#FEEBEB]': attendance.status === 'late',
                            'bg-[#DAF8E6]': attendance.status === 'present',
                            // 'bg-[#FFFBEB]': attendance.status === 'leave',
                        },
                    )}
                >
                    {attendance.status}
                </button>
            </div>
        </div>
    );
};

export default AttendanceCard;
